@import url(https://fonts.googleapis.com/css2?family=Pattaya&family=Roboto&display=swap);
*{box-sizing:border-box;margin:0;padding:0}body{width:100vw;font-family:"Roboto";color:#fff}li{list-style-type:none}a{text-decoration:none}a:visited{color:#383324}.navigation{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;background-color:#c0a76d;top:0;height:auto}.navigation a{margin-top:30px;margin-right:20px;margin-left:20px;padding-bottom:10px;margin-bottom:30px;color:#fff;position:relative}.navigation .nav-active::after{content:'';width:100%;height:10%;background-color:#383324;position:absolute;bottom:1px;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);transition:0.2s}.logo{display:-webkit-flex;display:flex;position:absolute;left:10px;width:100px;height:100px;z-index:10}.carte{height:100vh;width:100%}.mapbox{width:100%;height:100%}.map-container{height:86vh}.sidebar{background-color:rgba(35,55,75,0.9);color:#ffffff;padding:6px 12px;font-family:monospace;z-index:1;position:absolute;top:200;left:0;margin:12px;border-radius:4px}

